(function() {
  'use strict';
  angular.module('formioAppTemplate', [
    'ngSanitize',
    'ui.router',
    'ui.bootstrap',
    'formio'
  ]);
})();

(function() {
  'use strict';

  angular
    .module('formioAppTemplate')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController() {}
})();

(function() {
  'use strict';

  angular
    .module('formioAppTemplate')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {
    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('formioAppTemplate')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider, FormioProvider, AppConfig) {
    FormioProvider.setBaseUrl(AppConfig.apiUrl);

    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global moment:false */
(function() {
  'use strict';
  angular
    .module('formioAppTemplate')
    .constant('moment', moment);
})();

(function() {
  'use strict';

  angular
    .module('formioAppTemplate')
    .config(config);

  /** @ngInject */
  function config($logProvider) {
    $logProvider.debugEnabled(true);
  }

})();

angular.module("formioAppTemplate").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"row\"><div class=\"jumbotron\"><div class=\"row\"><div class=\"col-sm-6 text-center\"><p class=\"lead\">You can easily embed your Forms and Resources into this application using.</p><div><pre>&lt;formio src=\"\'https://yourapp.form.io/user/login\'\"&gt;&lt;/formio&gt;</pre></div><p class=\"lead\">Need Help?</p><ul class=\"list-inline\"><li><a class=\"btn btn-lg btn-success\" target=\"_blank\" href=\"http://help.form.io/embedding/\">Embedding</a></li><li><a class=\"btn btn-lg btn-success\" target=\"_blank\" href=\"http://help.form.io\">Documentation</a></li></ul></div><div class=\"col-sm-6\"><p class=\"lead\">Example Form</p><div class=\"well\"><formio src=\"\'https://formio.form.io/contact\'\"></formio></div></div></div></div></div>");}]);